import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppComponent } from './app.component';
import { IdModalComponent } from './id-modal/id-modal.component';
import { HomeAccessComponent } from './home-access/home-access.component';
import { HomeCameraComponent } from './home-camera/home-camera.component';
import { IdFrontComponent } from './id-front/id-front.component';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
    { path: 'id-modal', component: IdModalComponent },
    { path: 'home-access', component: HomeAccessComponent },
    { path: 'home-camera', component: HomeCameraComponent },
    { path: 'id-front', component: IdFrontComponent }
];

@NgModule({
    declarations: [AppComponent, IdModalComponent, HomeAccessComponent, HomeCameraComponent, IdFrontComponent],
    imports: [BrowserModule, RouterModule.forRoot(routes)],
    providers: [],
    bootstrap: [AppComponent],
    exports: [RouterModule],
    schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class AppModule {}
